/**
 * Social Media Colors
 * @variables
 */
body {
  background: #bfbfbf;
  background: -moz-linear-gradient(left, #bfbfbf 0%, #f2f2f2 40%, #f2f2f2 60%, #cacaca 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #bfbfbf), color-stop(40%, #f2f2f2), color-stop(60%, #f2f2f2), color-stop(100%, #cacaca));
  background: -webkit-linear-gradient(left, #bfbfbf 0%, #f2f2f2 40%, #f2f2f2 60%, #cacaca 100%);
  background: -o-linear-gradient(left, #bfbfbf 0%, #f2f2f2 40%, #f2f2f2 60%, #cacaca 100%);
  background: -ms-linear-gradient(left, #bfbfbf 0%, #f2f2f2 40%, #f2f2f2 60%, #cacaca 100%);
  background: linear-gradient(to right, #bfbfbf 0%, #f2f2f2 40%, #f2f2f2 60%, #cacaca 100%);
}

.master-container {
  max-width: 980px;
  margin: auto;
  padding: 0;
}

.container--default {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.primary-footer {
  background: #333;
  padding-bottom: 2em;
}

.container-padding {
  padding-bottom: 3em;
}

.container-dark-grey {
  background: #ccc;
}

.footer-nav {
  background: none;
}

/**
     * Nav title
     *
     */
.nav-title {
  padding-top: 1em;
  color: #fff;
  border-bottom: thin solid #fff;
  text-transform: capitalize;
}

.nav-title:before, .nav-title:after {
  display: table;
  content: "";
  clear: both;
}

.footer-nav--list {
  margin-bottom: 1em;
}

.footer-nav--list ul {
  padding-left: 0;
  list-style-type: none;
  padding-top: 0.25em;
}

.footer-nav--list li {
  padding-left: 0;
  line-height: 2;
}

.footer-nav--list li a {
  color: #fff;
  font-size: 11px;
  text-transform: capitalize;
  cursor: pointer;
  display: block;
}

.primary-header--links {
  background: #8B0E1A;
}

.dropdown-menu > li > a,
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:active {
  color: #BE1E2D;
  font-weight: 600;
  line-height: 1.5;
}

.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:active {
  background: #e6e7e8;
}

.nav-pills > li > a {
  border-radius: 0;
  font-weight: 600;
}

.nav-pills > li > a:hover {
  font-weight: 600;
}

.master-container .navbar-nav > li > a {
  border-left: 0.5px solid #ddd;
}

/***********************************************      Modal Styles
     \***********************************************/
#myModal {
  position: absolute;
  left: 350px;
  top: 150px;
  width: 200px;
  height: 100px;
}

#wowslider-container2 {
  max-width: 980px !important;
}

.container--header {
  margin-bottom: 20px;
  border-bottom: 5px solid #666;
}

.dropdown-menu > li > a {
  padding: 0.65em 0.65em;
}

.bt-5 {
  border-top: 0.3125em solid #666;
}

.mt-20 {
  margin-top: 1.25em;
}

.mt-10 {
  margin-top: 0.625em;
}

.mt-5 {
  margin-top: 0.3125em;
}

.mt-30 {
  margin-top: 1.875em;
}

/**
      * Social Icon Menu
      * Socal Media Icons from FontAwesome
      *
      */
.social--navigation {
  margin-top: 0.75em;
  margin-bottom: 0;
}

.social--navigation li {
  margin-top: 0.75em;
  display: inline-block;
  padding-left: 1.2em;
}

.social--navigation li::first-child {
  padding-left: 0;
}

.fa-twitter-square {
  color: #00aced;
}

.fa-facebook-square {
  color: #3b5998;
}

.fa-youtube-square {
  color: #bb0000;
}

.fa-google-plus-square {
  color: #dd4b39;
}

.fa-linkedin-square {
  color: #007bb6;
}

/**
       * Featured Social Media Section
       *
       */
.featured-section--social {
  max-height: 13.3em;
  overflow-y: scroll;
}

header[role="banner"] #search {
  float: right;
  margin-top: 9px;
  width: 250px;
}

header[role="banner"] .search {
  padding: 5px 0;
  width: 230px;
  height: 30px;
  position: relative;
  left: 10px;
  float: left;
  line-height: 22px;
}

header[role="banner"] .search input {
  position: absolute;
  width: 0px;
  float: Left;
  margin-left: 210px;
  -webkit-transition: all 0.7s ease-in-out;
  -moz-transition: all 0.7s ease-in-out;
  -o-transition: all 0.7s ease-in-out;
  transition: all 0.7s ease-in-out;
  height: 30px;
  line-height: 18px;
  padding: 0 2px 0 2px;
  border-radius: 1px;
}

header[role="banner"] .search:hover input, header[role="banner"] .search input:focus {
  width: 200px;
  margin-left: 0px;
}

header[role="banner"] .btn {
  height: 30px;
  position: absolute;
  right: 0;
  top: 5px;
  border-radius: 1px;
}

